.advantages-item-advanced {
  background-color: $primaryColor;
  color: #ffffff;
  border: 0;
  box-sizing: border-box;

  &:first-child::before {
    position: absolute;
    top: -1px;
    display: block;
    width: 100%;
    height: 37px;
    background-image: url("/img/white-devider-main.svg");
    background-repeat: no-repeat;
    background-position: center;
    content: "";

    @media screen and (min-width: $tablet-width) {
      display: none;
    }
  }

  @media screen and (min-width: $tablet-width) {
    display: grid;
    grid-template-columns: 1fr 2fr;
  }
}

.advantages-item-advanced--reverse {
  display: flex;
  flex-direction: column-reverse;

  @media screen and (min-width: $tablet-width) {
    display: grid;
    grid-template-columns: 2fr 1fr;
  }
}

.advantages-item-advanced__content-wrapper {
  display: flex;
  flex-direction: column;
  padding: 39px 16px 35px 16px;
  text-align: center;
  border-bottom: 1px solid #d6d6d6;

  &--first {
    padding: 71px 20px 40px 18px;
  }

  @media screen and (min-width: $tablet-width) {
    justify-content: center;
    align-items: center;
    border: 0;
  }
}

.advantages-item-advanced__title {
  margin: 0;
  margin-bottom: 11px;
  color: #ffffff;
  text-transform: uppercase;

  @include font-style(18px, 26px, 700);
  @include add-number;

  @media screen and (min-width: $tablet-width) {
    @include font-style(21px, 26px, 700);
  }
}

.advantages-item-advanced__description {
  margin: 0;
  text-transform: uppercase;

  @include font-style(14px, 21px, 400);
}

.advantages-item-advanced__photo {
  display: block;
  padding: 0;
  width: 100%;
  object-fit: cover;
}

.advantages-item-advanced__sublist {
  grid-column: 1 / span 3;
}
